html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  color: #dedede;
  background-color: #3a3a3a;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.colbox {
  flex-direction: row;
  display: flex;
}

.rowbox {
  flex-direction: column;
  display: flex;
}

#header {
  background: linear-gradient(#111, #0000);
  height: 500px;
  position: relative;
}

.section {
  opacity: 0;
  width: 100%;
  padding-top: 30px;
  transition: all .5s;
  position: relative;
  transform: translateY(50px);
}

.content {
  text-align: left;
  flex-direction: column;
  flex-grow: 1;
  align-items: flex-start;
  margin-left: 50px;
  font-size: 30px;
  line-height: 27px;
  display: flex;
}

.center {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.active {
  opacity: 1;
  transform: none;
}

.subtitle {
  flex-grow: 1;
  width: 20%;
  position: relative;
}

.marker {
  opacity: 0;
  cursor: pointer;
  color: #8a8a8a;
  margin-right: 7px;
  font-size: 20px;
  text-decoration: none;
  transition: all .5s;
  position: absolute;
  top: 3px;
  left: 7px;
}

#footer {
  height: 100px;
}

.footer {
  margin-left: 50px;
  margin-right: 50%;
  font-size: 14px;
  font-weight: 100;
}

@media screen and (orientation: portrait) {
  #header {
    height: 350px;
  }

  .colbox {
    flex-direction: column;
    display: flex;
  }

  .rowbox {
    flex-direction: row;
    display: flex;
  }
}

